@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

/* base styles */
body {
  font-family: Poppins, sans-serif;
  margin: 0;
  font-size: 1.1em;
}

ul,
li,
p,
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

.btn {
  background: none;
  border: 2px solid #1f9751;
  padding: 6px 12px;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}

.btn1 {
  background: none;
  border: 2px solid #1f9751;
  padding: 6px 6px;
  margin-left: 17px;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;

  font-size: 1em;
}

.search {
  background: none;

  padding: 6px 6px;
  cursor: pointer;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;

  font-size: 1em;
}




.btn:hover {
  background: #1f9751;
  color: #fff;
}

.title {
  display: flex;
  justify-content: space-between;
  margin-inline: 1em;
  color: #1f9751;
}

.bookedBy {
  margin-left: auto;
  margin-right: 0;
}

.middle {
  margin-left: auto;
  margin-right: auto;
}

.mj {
  color: #1f9751;
}